import {Component, ViewChild, AfterViewInit, ElementRef, Input} from '@angular/core';
import {Observable} from 'rxjs/Observable';

@Component({
  selector: 'drawer',
  templateUrl: './drawer.component.html',
  styleUrls: ['./drawer.component.css'],
})
export class DrawerComponent implements AfterViewInit {

  context: CanvasRenderingContext2D;

  @ViewChild('canvasDrawer') myCanvas: ElementRef;
  @Input() public width = 700;
  @Input() public height = 600;

  public ngAfterViewInit() {
    const canvasEl: HTMLCanvasElement = this.myCanvas.nativeElement;
    this.context = canvasEl.getContext('2d');

    canvasEl.width = this.width;
    canvasEl.height = this.height;

    this.context.lineWidth = 3;
    this.context.lineCap = 'round';
    this.context.strokeStyle = '#343a40';

    this.captureEvents(canvasEl);
  }
  private captureEvents(canvasEl: HTMLCanvasElement) {
    Observable
      .fromEvent(canvasEl, 'mousedown')
      .switchMap((e) => {
        return Observable
          .fromEvent(canvasEl, 'mousemove')
          .takeUntil(Observable.fromEvent(canvasEl, 'mouseup'))
          .takeUntil(Observable.fromEvent(canvasEl, 'mouseleave'))
          .pairwise();
      })
      .subscribe((res: [MouseEvent, MouseEvent]) => {
        const rect = canvasEl.getBoundingClientRect();

        const prevPos = {
          x: res[0].clientX - rect.left,
          y: res[0].clientY - rect.top
        };

        const currentPos = {
          x: res[1].clientX - rect.left,
          y: res[1].clientY - rect.top
        };

        this.drawOnCanvas(prevPos, currentPos);
      });
  }
  private drawOnCanvas(prevPos: { x: number, y: number }, currentPos: { x: number, y: number }) {
    if (!this.context) { return; }
    this.context.beginPath();
    if (prevPos) {
      this.context.moveTo(prevPos.x, prevPos.y); // from
      this.context.lineTo(currentPos.x, currentPos.y);
      this.context.stroke();
    }
  }
  setStrokeColor(color) {
    this.context.strokeStyle = color;
  }
  clearCanvas() {
    this.context.clearRect(0, 0, this.context.canvas.width, this.context.canvas.height); // Clears the canvas
  }
}



